<template>
  <Input v-model="data.backmarket_key" :view="validation.backmarket_key.$invalid ? 'danger' : 'default'" class="my-4" label="BackMarket Private API Key" placeholder="" block />
  <small class="text-red-base block mb-4" v-for="(error, errorIndex) of validation.backmarket_key.$errors" :key="errorIndex">{{ error.$message }}. </small>
</template>

<script>
import { defineComponent } from 'vue'
import { useShowAll } from 'composable/integration'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import { ElMessage } from 'element-plus'
import { isFunction } from '@/utils/is'

export default defineComponent({
  components: {
    Button,
    Alert,
    Input
  },
  async setup(){
    const { state, data, actionAll, actionUpdate, loading } = useShowAll()
    const rules = {
      backmarket_key: {
        required
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const update = async (callback=()=>{}) => {
      const isValid = validate()
      if( !isValid ) return
      const payload = Object.assign({}, {
        backmarket_key: data.backmarket_key
      })
      await actionUpdate(payload, (response) => {
        if( response.toLowerCase() === 'connection failed' ) return ElMessage({
          type: 'warning',
          message: 'Invalid BackMarket API Key'
        })
        ElMessage({
          type: 'success',
          message: 'BackMarket integration updated!'
        })
        isFunction(callback) && callback()
      })
    }

    await Promise.all([
      actionAll()
    ])

    return {
      state,
      data,
      loading,
      validation,
      update
    }
  }
})
</script>